.field-type {
  border-bottom-left-radius: 10px;
  border-top: 2px solid #DEDEDE;
  padding: 30px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.field-type .rowView {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding: 2px;
  margin-bottom: 15px;
  min-width: 140px;
  -webkit-align-items: center;
          align-items: center;
}
.field-type .rowView .icon {
  width: 35px;
  height: 35px;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.field-type .rowView .icon img {
  width: 18px;
  background-size: contain;
  background-repeat: no-repeat;
}
.field-type .rowView .text {
  margin-left: 10px;
  line-height: 17px;
  font-size: 15px;
  color: #000000;
}
