.TableView {
  height: 100%;
}
.TableView .ant-table-filter-column-title {
  padding-right: 16px !important;
}
.TableView .ant-table-container {
  border-left: 1px solid #2E2E2E !important;
  border-top: 1px solid #2E2E2E !important;
}
.TableView tbody .ant-table-thead > tr > th,
.TableView .ant-table-tbody > tr > td,
.TableView .ant-table tfoot > tr > th,
.TableView .ant-table tfoot > tr > td {
  padding: 6px 16px;
}
.TableView .ant-table-filter-column {
  margin: 0px;
}
.TableView .ant-table-column-sorter-full {
  position: absolute;
  right: 10px;
  background-color: transparent;
}
.TableView .ant-table-column-sorters {
  width: 100%;
}
.TableView .ant-table-column-sorters span:nth-child(1) {
  width: 100%;
  background-color: transparent !important;
}
.TableView .ant-table-column-sorters,
.TableView .ant-table-filter-column-title {
  padding: 0px !important;
}
.TableView thead th {
  padding: 0px 0px;
  border-bottom: 3px solid #000000 !important;
  border-right: 1px solid #2E2E2E !important;
  cursor: pointer;
}
.TableView thead th p {
  padding: 6px 0px 10px 0px;
  font-size: 18px;
  color: #000000;
  text-align: left;
  margin: 0px;
  line-height: 20px;
}
.TableView thead th .ID {
  background-color: #e4e0e0;
}
.TableView thead th .numerical {
  background-color: rgba(187, 222, 165);
}
.TableView thead th .categorical {
  background-color: rgba(237, 185, 154);
}
.TableView thead th .temporal {
  background-color: rgba(240, 220, 159);
}
.TableView thead th .geographical {
  background-color: rgba(179, 205, 231);
}
.TableView thead th :hover {
  background-color: #fafafa !important;
  transition: background-color 0.3s;
}
.TableView tbody td {
  border-bottom: 1px solid #2E2E2E;
  border-right: 1px solid #2E2E2E !important;
}
.TableView tbody .numerical {
  background-color: rgba(226, 239, 218);
}
.TableView tbody .categorical {
  background-color: rgba(252, 228, 213);
}
.TableView tbody .temporal {
  background-color: rgba(255, 242, 204);
}
.TableView tbody .geographical {
  background-color: rgba(221, 234, 246);
}
.TableView tbody .ID {
  background-color: rgba(244, 244, 244);
}
.TableView .title {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.TableView .field-icon {
  margin-top: 13px;
  width: 41px;
  height: 14px;
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
}
.TableView .field-ID {
  background-image: url(data:image/png;base64,);
}
.TableView .field-temporal {
  background-image: url(data:image/png;base64,);
}
.TableView .field-categorical {
  background-image: url(/static/media/categorical.0455b53d.png);
}
.TableView .field-numerical {
  background-image: url(data:image/png;base64,);
}
.TableView .field-geographical {
  background-image: url(/static/media/geographical.c5034f5b.png);
}
